import { Company } from '@models/companies/company';

export interface ClientCard {
  id: number;
  name: string;
  surname: string;
  role: Role;
  additional: string;
  phone_city: string;
  mobile: string;
  another_mobile?: string;
  address: string;
  city: string;
  zipcode: number;
  email: string;
  comments: string;
  company: Company
}

export enum Role {
  Private,
  Referrer,
  Business,
  Institution,
  VIP
}
