import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ClientCard, Role } from '@models/clients/client-card';

@Component({
  selector: 'sim-add-client',
  templateUrl: './add-client.component.html',
  styleUrls: ['./add-client.component.css']
})
export class AddClientComponent implements OnInit {

  @Input() clientData?: ClientCard

  clientForm: FormGroup;
  types: string[] = [ Role[0], Role[1], Role[2], Role[3], Role[4], ];
  constructor(private fb: FormBuilder, private activeModal: NgbActiveModal) { }

  ngOnInit(): void {
    this.clientForm = this.fb.group({
      name:          [this.clientData?.name,          [Validators.required]],
      surname:       [this.clientData?.surname,       [Validators.required]],
      email:         [this.clientData?.email,         [Validators.email ,Validators.required]],
      mobile:        [this.clientData?.mobile,        [Validators.required]],
      another_mobile:[this.clientData?.another_mobile,[Validators.required]],
      role:          [Role[this.clientData?.role],    [Validators.required]],
      company:       [this.clientData?.company,       [Validators.required]],
      address:       [this.clientData?.address,       [Validators.required]],
      city:          [this.clientData?.city,          [Validators.required]],
      phone_city:    [this.clientData?.phone_city,    [Validators.required]],
      zipcode:       [this.clientData?.zipcode,       [Validators.required]],
      additional:    [this.clientData?.additional,    [Validators.required]],
      comments:      [this.clientData?.comments,      [Validators.required]],
    })  
  }

  onSubmit(): void {
    const resultData = this.clientForm.value;
    resultData.id = this.clientData?.id;
    resultData.role = Role[resultData.role];
    this.activeModal.close({ client: resultData, isCreate: this.clientData ? false : true });
  }

  onCancel(): void {
    this.activeModal.close();
  }}

