import { ServerDataSource } from 'ng2-smart-table';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ServerSourceConf } from 'ng2-smart-table/lib/lib/data-source/server/server-source.conf';

export class CustomServerDataSource extends ServerDataSource {

  public params: {key: any; value: any }[];
  protected requestElements(): Observable<any> {
    let params = this.createRequesParams();
    for (const obj of this.params) {
        params = params.append(obj.key, obj.value);
    }
    return this.http.get(this.conf.endPoint, { params, observe: 'response' });
  }

  constructor(http: HttpClient, conf: ServerSourceConf | {}, params: {key: any; value: any }[]) {
    super(http, conf);
    this.params = params;
  }
}
